<template lang="pug">
  .formulario-ocorrencia
    small.erro(v-if='!status && !ocorrencia.lat') Erro! Informe uma localização!
    p(v-if='endereco') Endereço: {{endereco}}
    form(v-on:submit.prevent="registrarOcorrencia")
      .dados
        .tipo
          controle-label(:valor="'Tipo da ocorrência'", for='tipoOcorrencia')
          controle-input(:tipo="'select'", :opcoes='opcoes', id='tipoOcorrencia', @inputValor='ocorrencia.tipo = $event.value')
          error-input(v-if='!status && !ocorrencia.tipo')
        .data
          controle-label(:valor="'Data'", for='dataOcorrencia')
          controle-input(:tipo="'input'", :type="'text'", :placeholder="'Ex.: 15/06/2015'", id='dataOcorrencia', @inputValor='ocorrencia.data = $event.value')
          error-input(v-if='!status && !ocorrencia.data')
        .horario
          controle-label(:valor="'Horário'", for='horarioOcorrencia')
          .campos
            controle-input(:tipo="'input'", :type="'number'", id='horarioOcorrencia', @inputValor='ocorrencia.horas = $event.value')
            span :
            controle-input(:tipo="'input'", :type="'number'", @inputValor='ocorrencia.minutos = $event.value')
          error-input(v-if='(!status && !ocorrencia.horas) || (!status && !ocorrencia.minutos)')
      .informacoes-adicionais
        titulo-internas Informações adicionais
        .assalto(v-if="ocorrencia.tipo == 'Assalto'")
          .prejuizo
            label Prejuízo estimado
            .valor
              span R$
              input(type="number")
            //- small Informe o valor
          .sexo
            label Sexo da vítima
            select
              option Selecione
              option Feminino
              option Masculino
            //- small Informe o sexo

        .arquivo
          controle-label(:valor="'Envie um anexo'", for='anexo')
          controle-input(:tipo="'input'", :type="'file'", id='anexo', @inputValor='ocorrencia.anexo = $event.value')

        .detalhes
          controle-label(:valor="'Ainda tem mais detalhes?'", for='detalhes')
          controle-input(:tipo="'textarea'", id='detalhes', rows='10', :placeholder="'Forneça mais detalhes sobre o ocorrido. (Opcional)'", @inputValor='ocorrencia.detalhes = $event.value')

        .botao
          button(type="submit") Registrar ocorrência agora

</template>

<script>
import TituloInternas from 'BASICS/TituloInternas'
import ControleLabel from 'BASICS/ControleLabel'
import ControleInput from 'BASICS/ControleInput'
import ErrorInput from 'BASICS/ErrorInput'

import { EventBus } from '../../scripts/utils/EventBus'

export default {
  components: {
    TituloInternas,
    ControleLabel,
    ControleInput,
    ErrorInput
  },
  created() {
    EventBus.$on('registro-ocorrencia', event => {
      this.ocorrencia.lat = event.lat
      this.ocorrencia.lng = event.lng
      this.endereco = event.end
    })
  },
  data() {
    return {
      status: true,
      ocorrencia: {
        tipo: '',
        data: '',
        horas: '',
        minutos: '',
        anexo: '',
        detalhes: '',
        lat: '',
        lng: ''
      },
      endereco: '',
      opcoes: ['Assalto', '2']
    }
  },
  methods: {
    validaFormulario() {
      this.status = true

      if(this.ocorrencia.tipo === '') {
        this.status = false
      } 
      
      if(this.ocorrencia.data === '') {
        this.status = false
      } 
      
      if(this.ocorrencia.horas === '') {
        this.status = false
      } else if(this.ocorrencia.horas !== '' && this.ocorrencia.minutos === '' ) {
        this.status = false
      }

      return this.status
    },
    resetForm() {

    },
    registrarOcorrencia() {
      if(this.validaFormulario()) {
        console.log(this.ocorrencia)
      }
    }
  }
}
</script>


<style lang="scss">
  .formulario-ocorrencia {
    margin: 20px 0;

    .erro {
      display: flex;
      justify-content: center;
      margin: 10px 0;
      font-size: 18px;
      font-weight: bold;
    }

    label {
      margin-bottom: 15px;
    }

    .dados {
      display: flex;

      .tipo {
        display: flex;
        flex-direction: column;
        width: 100%;
      }

      .data {
        display: flex;
        flex-direction: column;
        margin: 0 20px;
      }

      .horario {
        display: flex;
        flex-direction: column;

        .campos {
          display: flex;
          align-items: center;

          input {
            width: 50px;
          }

          span {
            margin: 0 5px;
          }
        }
      }
    }

    .informacoes-adicionais {
      margin-top: 20px;

      h2 {
        margin-bottom: 20px;
      }

      .assalto {
        display: flex;

        .prejuizo,
        .sexo {
          display: flex;
          flex-direction: column;
        }

        .prejuizo {
          margin-right: 20px;
          span {
            margin-right: 5px;
          }

          .valor {
            display: flex;
            align-items: center;
          }
        }
      }

      .arquivo ,
      .detalhes {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
      }
      

      .botao {
        display: flex;
        justify-content: flex-end;
        margin-top: 20px;

        button {
          color: #fff;
          background: $cor-verde-claro;
          padding: 10px 80px;
          border: none;
          border-radius: 5px;
          font-size: 16px;

          &:hover {
            cursor: pointer;
            opacity: 0.7;
          }
        }
      }
    }
  }
</style>




